/*
 City Stats Card Component
 */

.statsCard {
    min-width: 275px;
    border-radius: 0;
}

.cardHeader {
    text-align: center;
    margin-bottom: 0;
    border-bottom: 1px solid rgba(151, 151, 151, 0.27);
    padding: 4px 0 20px 0;
}

.gridRoot {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    overflow: hidden;
}

.gridTileRoot {
    border: 1px solid rgba(151, 151, 151, 0.27);
    padding: 0;
}

.gridTileTile {
    padding: 8px;
}

.gridTileRoot:nth-child(even) {
    border-right: none;
}

.gridTileRoot:nth-child(odd) {
    border-left: none;
}

.statsIcon {
    margin-top: 12px;
    margin-right: 8px;
    height: 30px;
    width: 30px;
    -ms-flex: 1 1;
        flex: 1 1;
    float: left;
}

.statDiv {
    max-width: 200px;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin: 0 auto;
}

.statDiv div {
    float: right;
}

.statDiv p {
    margin-top: 0;
    text-align: left;
}

/*
Pie Chart
 */
.pieChart {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    border-top: 1px solid rgba(151, 151, 151, 0.27);
    padding: 20px 0;
}

.pieChart svg {
    display: inline;
    height: 60%;
    width: 60%;
    margin-right: 20px;
}
/*
Horizontal Legend
 */

.horizontalLegend {
    text-align: center;
}
.horizontalLegend .horizontalLegend-title {
    margin-bottom: 8px;
}
.horizontalLegend .horizontalLegend-scale ul {
    margin: 0;
    padding: 0;
    list-style: none;
}
.horizontalLegend .horizontalLegend-scale ul li {
    display: block;
    float: left;
    width: 50px;
    margin-bottom: 6px;
    text-align: center;
    font-size: 80%;
    list-style: none;
}
.horizontalLegend ul.horizontalLegend-labels li span {
    display: block;
    float: left;
    height: 15px;
    width: 50px;
}

.horizontalLegend ul.horizontalLegend-labels {
    display:table;
    margin: 0 auto;
}

.horizontalLegend ul.horizontalLegend-labels li {
   display:inline;
}

/*
Legend

 */

.my-legend {
    display: inline;
    float: right;}

.my-legend .legend-title {
    text-align: left;
    margin-bottom: 5px;
}

.my-legend .legend-scale ul {
    margin: 0 0 5px;
    padding: 0;
    float: left;
    list-style: none;
}

.my-legend .legend-scale ul li {
    list-style: none;
    margin-left: 0;
    line-height: 18px;
    margin-bottom: 2px;
}

.my-legend ul.legend-labels li span {
    display: block;
    float: left;
    height: 16px;
    width: 30px;
    margin-right: 5px;
    margin-left: 0;
    border: 1px solid #999;
}

.my-legend div {
    font-family: 'Open Sans', sans-serif;
}


a.critigenLogo {
    position: relative;
}
a.critigenLogo:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left:0;
}


